import { unitStats } from '../data/unit-stats.const';

export interface AttackerData {
    hp: number;
    isVeteran: boolean;
    freeFromRetaliation: boolean;
    unit: string;
    carriedUnit: string;
}

export type DefenderData = AttackerData & {
    withDefenseBonus: 'withWall' | 'withoutWall';
};

export interface UnitStat {
    attack: number;
    defense: number;
    hp: number;
    veteranHp: number;
    defenseBonus: number;
    wallBonus: number;
}

export enum UnitFormType {
    ATTACKER = 'attacker',
    DEFENDER = 'defender',
}