import { Component, Input } from '@angular/core';
import { BattleResult, BattleResultStatistic } from '../../../models/battle-result.model';

@Component({
    selector: 'app-battle-result',
    templateUrl: 'battle-result.component.html',
    styleUrls: ['battle-result.component.scss']
})
export class BattleResultComponent {
    @Input() battleResults: BattleResult[];
    @Input() battleResultStatistics: BattleResultStatistic[];
    @Input() multi: boolean;
    @Input() showingOptimalResult: boolean;

    getAttacker(i) {
        return this.battleResults[i].attacker;
    }
    getDefender(i) {
        return this.battleResults[i].defender;
    }

    // get attackerHpColor() {
    //     return this.assessColor(this.attacker.newHp, this.defender.newHp);
    // }

    // get defenderHpColor() {
    //     return this.assessColor(this.defender.newHp, this.attacker.newHp);
    // }

    // get splashedHpColor() {
    //     return this.assessColor(this.defender.splashedNewHp, this.attacker.newHp);
    // }

    getAttackDamageColor(i) {
        return this.assessColor(this.getAttacker(i).attackDamage, this.getDefender(i).defenseDamage);
    }

    getDefenseDamageColor(i) {
        return this.assessColor(this.getDefender(i).defenseDamage, this.getAttacker(i).attackDamage);
    }

    assessColor(statA: number, statB: number) {
        if (statA > statB) {
            return 'success';
        } else if (statA < statB) {
            return 'danger';
        } else {
            return null;
        }
    }

    getOrderSuperscript(i) {
        const orderNumber = i + 1;
        if (orderNumber % 10 === 1) {
            return `st`;
        } else if (orderNumber % 10 === 2) {
            return `nd`;
        } else if (orderNumber % 10 === 3) {
            return `rd`;
        } else {
            return `th`;
        }
    }

}
