import { FormGroup, ValidationErrors } from '@angular/forms';
import { carryUnits } from '../data/unit-list.const';

export function carriedUnitValidator(fg: FormGroup): ValidationErrors {
    const unit = fg.get('unit').value;
    if (carryUnits.includes(unit) && !fg.get('carriedUnit').value) {
        return { required: true, name: 'carriedUnit' };
    }
    return null;
}
