import { AttackerData, DefenderData } from '../models/unit-data.model';
import { unitStats as UNIT_STATS } from '../data/unit-stats.const';
import { calculateMaxHp } from './calculate-max-hp.util';

export function calculateDamage(attacker: AttackerData, defender: DefenderData) {
  const attackerStat = UNIT_STATS[attacker.unit];
  const defenderStat = UNIT_STATS[defender.unit];

  const attackerMaxHp = calculateMaxHp(attacker);
  const defenderMaxHp = calculateMaxHp(defender);

  const attackForce = attackerStat.attack * (attacker.hp / attackerMaxHp);

  let defenseForce = defenderStat.defense * (defender.hp / defenderMaxHp);
  if (defender.withDefenseBonus) {
    const defenseBonus = defender.withDefenseBonus === 'withWall' ? defenderStat.wallBonus : defenderStat.defenseBonus;
    if (defenseBonus) {
      defenseForce *= defenseBonus;
    }
  }

  const totalForce = attackForce + defenseForce;
  const accelerator = 4.5;

  const attackDamage = Math.round((attackForce / totalForce) * attackerStat.attack * accelerator);
  let defenseDamage = Math.round((defenseForce / totalForce) * defenderStat.defense * accelerator);

  const defenderNewHp = Math.max(defender.hp - attackDamage, 0);
  let splashedNewHp = null;
  if (attacker.unit === 'Fire Dragon') {
    splashedNewHp = Math.max(defender.hp - Math.floor(attackDamage / 2), 0);
  }
  let attackerNewHp = attacker.hp;
  const noRetalaiationUnits = ['Mooni', 'Ice Archer'];
  const freezingUnits = ['Ice Archer'];
  const noRetaliation = attacker.freeFromRetaliation || noRetalaiationUnits.includes(defender.unit) || freezingUnits.includes(attacker.unit);
  if (defenderNewHp > 0 && !noRetaliation) {
    attackerNewHp = Math.max(attacker.hp - defenseDamage, 0);
  } else {
    defenseDamage = 0;
  }

  return { attackDamage, defenseDamage, defenderNewHp, attackerNewHp, splashedNewHp };
}
