import { AttackerData, DefenderData, UnitStat } from '../models/unit-data.model';
import { unitStats as UNIT_STATS } from '../data/unit-stats.const';
import { carryUnits } from '../data/unit-list.const';

export function calculateMaxHp(unit: AttackerData | DefenderData) {
    if (!unit.unit) {
        return null;
    }

    // For units with carry skills, such as Boat, Ship and Battleship
    // their health and maxHealth varies based on the unit they are carrying
    // so we will use the carried unit stat for getting maxHp
    let unitStat;
    if (carryUnits.includes(unit.unit)) {
        if (!unit.carriedUnit) {
            return null;
        }
        unitStat = UNIT_STATS[unit.carriedUnit];
    } else {
        unitStat = UNIT_STATS[unit.unit];
    }
    return unit.isVeteran && unitStat.veteranHp ? unitStat.veteranHp : unitStat.hp;
}