import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ControlContainer, FormGroup } from '@angular/forms';
import { IonInput } from '@ionic/angular';
import { merge, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UnitFormType } from 'src/app/models/unit-data.model';
import { calculateMaxHp } from 'src/app/utils/calculate-max-hp.util';
import { attackerUnits, defenderUnits, landUnits, carryUnits, longRangeUnits } from '../../../data/unit-list.const';
import { unitStats } from '../../../data/unit-stats.const';

@Component({
  selector: 'app-unit-form',
  templateUrl: './unit-form.component.html',
  styleUrls: ['./unit-form.component.scss'],
})
export class UnitFormComponent implements OnInit, OnDestroy {

  @Input() unitFormType: UnitFormType;
  @Input() multi: boolean;
  @ViewChild('hpInputComponent', { static: true }) hpInputComponent: IonInput;
  @ViewChild('countInputComponent', { static: false }) countInputComponent: IonInput;

  unitOptions: string[];
  carriedUnitOptions: string[] = landUnits;
  form: FormGroup;
  maxHp: number;

  private onDestroy$ = new Subject<void>();

  constructor(private controlContainer: ControlContainer) { }

  ngOnInit() {
    const isAttackerFormType = this.unitFormType === UnitFormType.ATTACKER;
    this.unitOptions = isAttackerFormType ? attackerUnits : defenderUnits;
    this.form = this.controlContainer.control as FormGroup;
    this.setMaxHp();

    const hpInput = this.form.get('hp')
    this.form.get('isVeteran').valueChanges
      .pipe(takeUntil(this.onDestroy$))
      .subscribe(() => {
        this.setMaxHp();
        if (hpInput.pristine || !hpInput.valid) {
          this.form.get('hp').reset(this.maxHp);
        }
      });

    merge(
      this.form.get('unit').valueChanges,
      this.form.get('carriedUnit').valueChanges
    )
      .pipe(takeUntil(this.onDestroy$))
      .subscribe(() => {
        this.setMaxHp();
        hpInput.reset(this.maxHp);
      });
  }

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

  get selectedUnit() {
    return this.form.get('unit').value;
  }

  get selectedCarriedUnit() {
    return this.form.get('carriedUnit').value;
  }

  get hasVeterance() {
    if (this.hasCarrySkill) {
      // if unit carries another unit, check the carried unit veterance
      const carriedUnit = this.form.get('carriedUnit').value;
      return unitStats[carriedUnit].veteranHp;
    }
    return unitStats[this.selectedUnit].veteranHp;
  }

  get hasCarrySkill() {
    return carryUnits.includes(this.selectedUnit);
  }

  get isLongRangeUnit() {
    return longRangeUnits.includes(this.selectedUnit);
  }

  get isDefenderForm() {
    return this.unitFormType === UnitFormType.DEFENDER;
  }
  
  setMaxHp() {
    this.maxHp = calculateMaxHp(this.form.getRawValue());
  }

  async decreaseHp() {
    const hpInput = this.form.get('hp');
    const curValue = hpInput.value || 0;
    if (curValue > 0) {
      hpInput.setValue(curValue - 1);
      (await this.hpInputComponent.getInputElement()).focus();
    }
  }

  async increaseHp() {
    const hpInput = this.form.get('hp');
    const curValue = hpInput.value || 0;
    hpInput.setValue(curValue + 1);
    (await this.hpInputComponent.getInputElement()).focus();
  }

  async decreaseSpawnCount() {
    const countInput = this.form.get('spawnCount');
    const curValue = countInput.value || 0;
    if (curValue > 0) {
      countInput.setValue(curValue - 1);
      (await this.countInputComponent.getInputElement()).focus();
    }
  }

  async increaseSpawnCount() {
    const countInput = this.form.get('spawnCount');
    const curValue = countInput.value || 0;
    countInput.setValue(curValue + 1);
    (await this.countInputComponent.getInputElement()).focus();
  }

  async selectText(event) {
    const input = await event.target.getInputElement();
    input.select();
  }
}
