import { ChangeDetectorRef, Component, EventEmitter, OnInit, Output } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { debounceTime, switchMap, takeUntil, tap } from 'rxjs/operators';

import { unitStats } from '../../../data/unit-stats.const';
import { GoogleAnalyticsService } from '../../../services/google-analytics.service';
import { carriedUnitValidator } from '../../../validators/carried-unit.validator';
import { maxHpValidator } from '../../../validators/max-hp.validator';
import { 
  attackerUnits as ATTACKER_UNITS,
  defenderUnits as DEFENDER_UNITS,
  landUnits as LAND_UNITS
} from '../../../data/unit-list.const';
import { BattleResult, BattleResultStatistic } from '../../../models/battle-result.model';
import { executeBattleMatches } from '../../../utils/execute-battle-matches.util';


@Component({
  selector: 'app-battle',
  templateUrl: './battle.component.html',
  styleUrls: ['./battle.component.scss'],
})
export class BattleComponent implements OnInit {

  attackerUnits: string[] = ATTACKER_UNITS;
  defenderUnits: string[] = DEFENDER_UNITS;
  landUnits: string[] = LAND_UNITS;
  battleForm: FormGroup;
  battleResults: BattleResult[];
  battleResultStatistics: BattleResultStatistic;
  isExecutingBattleMatches: boolean;

  @Output() change = new EventEmitter<BattleResult[]>();

  private onDestroy$ = new Subject<void>();
  
  constructor(
    private fb: FormBuilder,
    private cdr: ChangeDetectorRef,
    private googleAnalytics: GoogleAnalyticsService
  ) {

    this.battleForm = this.fb.group({
      attackers: fb.array([this.createAttackerForm()]),
      defenders: fb.array([this.createDefenderForm()]),
      getOptimalResult: true
    });

    this.battleForm.valueChanges
      .pipe(
        debounceTime(300),
        takeUntil(this.onDestroy$),
        tap(() => {
          this.isExecutingBattleMatches = true;
        }),
        switchMap(() => this.showBattleResults())
      )
      .subscribe(({ battleResults, battleResultStatistics}) => {
        this.battleResults = battleResults;
        this.battleResultStatistics = battleResultStatistics;
        this.change.emit(battleResults);
        this.isExecutingBattleMatches = false;
        this.cdr.detectChanges();
      })
  }

  get attackerForms() {
    return (this.battleForm.get('attackers') as FormArray).controls;
  }

  get defenderForms() {
    return (this.battleForm.get('defenders') as FormArray).controls;
  }

  createAttackerForm() {
    const attacker = this.attackerUnits[0];
    const attackerStat = unitStats[attacker];
    const carriedUnit = this.landUnits[0];

    return this.fb.group({
        unit: attacker,
        carriedUnit: carriedUnit,
        hp: [attackerStat.hp, [Validators.required, Validators.min(0)]],
        isVeteran: false,
        freeFromRetaliation: false,
        spawnCount: [1, Validators.min(1)]
      }, { validators: [maxHpValidator, carriedUnitValidator] })
  }

  createDefenderForm() {
    const defender = this.defenderUnits[0];
    const defenderStat = unitStats[defender];
    const carriedUnit = this.landUnits[0];

    return this.fb.group({
      unit: defender,
      carriedUnit: carriedUnit,
      hp: [defenderStat.hp, [Validators.required, Validators.min(0)]],
      isVeteran: false,
      withDefenseBonus: false,
      withWallBonus: false,
      spawnCount: [1, Validators.min(1)]
    }, { validators: [maxHpValidator, carriedUnitValidator] })
  }

  addOffenseUnit() {
    (this.battleForm.get('attackers') as FormArray).push(this.createAttackerForm());
  }
  addDefenseUnit() {
    (this.battleForm.get('defenders') as FormArray).push(this.createDefenderForm());
  }

  removeOffenseUnit(i) {
    (this.battleForm.get('attackers') as FormArray).removeAt(i);
  }
  removeDefenseUnit(i) {
    (this.battleForm.get('defenders') as FormArray).removeAt(i);
  }

  showBattleResults() {
    return new Observable((subscriber) => {
      if (!this.battleForm.valid) {
        subscriber.next({});
        subscriber.complete();
        return;
      }

      const attackers = this.getUnits('attackers');
      const defenders = this.getUnits('defenders');


      this.googleAnalytics.emitEvent(
        'battle calculate', 'method', `${attackers.map(attacker => attacker.unit).join(', ')} vs ${defenders.map(defender => defender.unit).join(', ')}`,
        { attackers: attackers, defenders: defenders }, 1);

      const getOptimalResult = this.battleForm.get('getOptimalResult').value;

      if (typeof Worker !== 'undefined') {
        // Create a new
        const battleMatchesWorker = new Worker('./battle.worker', { type: 'module' });
        battleMatchesWorker.onmessage = ({ data }) => {
          subscriber.next(data);
          subscriber.complete();
          battleMatchesWorker.terminate();
        };
        battleMatchesWorker.postMessage({attackers, defenders, getOptimalResult});

        return {
          unsubscribe: () => {
            subscriber.complete();
            battleMatchesWorker.terminate();
          }
        }
      } else {
        // Web Workers are not supported in this environment.
        // You should add a fallback so that your program still executes correctly.
        subscriber.next(executeBattleMatches(attackers, defenders, getOptimalResult));
        subscriber.complete();  
      }
    });

  }

  getUnits(unitType: 'attackers' | 'defenders') {
    const units = [];
    this.battleForm.get(unitType).value.forEach((unit) => {
      for (let i = 0; i < unit.spawnCount; i++) {
        units.push({...unit});
      }
    });
    return units;
  }


  ngOnInit() {}

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

}

