import { FormGroup, ValidationErrors } from '@angular/forms';
import { calculateMaxHp } from '../utils/calculate-max-hp.util';

export function maxHpValidator(fg: FormGroup): ValidationErrors {
    const maxHp = calculateMaxHp(fg.value);
    const hp = fg.get('hp').value;
    if (hp <= maxHp) {
        return null;
    }

    const errors = { max: true };
    fg.get('hp').setErrors(errors);
    return errors;
}