import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { debounceTime, takeUntil } from 'rxjs/operators';

import { attackerUnits as ATTACKER_UNITS, defenderUnits as DEFENDER_UNITS, landUnits as LAND_UNITS } from '../../../data/unit-list.const';
import { maxHpValidator } from '../../../validators/max-hp.validator';
import { calculateDamage } from '../../../utils/calculate-damage.util';
import { unitStats } from '../../../data/unit-stats.const';
import { carriedUnitValidator } from '../../../validators/carried-unit.validator';
import { GoogleAnalyticsService } from '../../../services/google-analytics.service';
import { BattleResult } from '../../../models/battle-result.model';

@Component({
  selector: 'app-one-on-one',
  templateUrl: 'one-on-one.component.html',
  styleUrls: ['one-on-one.component.scss'],
})
export class OneOnOneComponent implements OnInit, OnDestroy {

  attackerUnits: string[] = ATTACKER_UNITS;
  defenderUnits: string[] = DEFENDER_UNITS;
  landUnits: string[] = LAND_UNITS;

  oneOnOneForm: FormGroup;

  battleResult: BattleResult;
  @Output() change = new EventEmitter<BattleResult>();

  private onDestroy$ = new Subject<void>();

  constructor(
    fb: FormBuilder,
    private googleAnalytics: GoogleAnalyticsService
  ) {
    const attacker = this.attackerUnits[0];
    const attackerStat = unitStats[attacker];
    const defender = this.defenderUnits[0];
    const defenderStat = unitStats[defender];
    const carriedUnit = this.landUnits[0];

    this.oneOnOneForm = fb.group({
      attacker: fb.group({
        unit: attacker,
        carriedUnit: carriedUnit,
        hp: [attackerStat.hp, [Validators.required, Validators.min(0)]],
        isVeteran: false,
        freeFromRetaliation: false,
      }, { validators: [maxHpValidator, carriedUnitValidator] }),
      defender: fb.group({
        unit: defender,
        carriedUnit: carriedUnit,
        hp: [defenderStat.hp, [Validators.required, Validators.min(0)]],
        isVeteran: false,
        withDefenseBonus: false,
        withWallBonus: false
      }, { validators: [maxHpValidator, carriedUnitValidator] })
    });

    this.oneOnOneForm.valueChanges
      .pipe(
        debounceTime(300),
        takeUntil(this.onDestroy$)
      )
      .subscribe(() => {
        this.showBattleResult();
      })
  }

  ngOnInit(): void { }

  ngOnDestroy(): void {
    this.onDestroy$.next();
    this.onDestroy$.complete();
  }

  async showBattleResult() {

    if (!this.oneOnOneForm.valid) {
      this.battleResult = null;
      this.change.emit(this.battleResult);
      return;
    }

    const attackerData = this.oneOnOneForm.get('attacker').value;
    const defenderData = this.oneOnOneForm.get('defender').value;
    const { attackDamage, defenseDamage, defenderNewHp, attackerNewHp, splashedNewHp } = calculateDamage(attackerData, defenderData);

    attackerData.origHp = attackerData.hp;
    attackerData.newHp = attackerNewHp;
    attackerData.attackDamage = Math.min(attackDamage, defenderData.hp - defenderNewHp);

    defenderData.origHp = defenderData.hp;
    defenderData.newHp = defenderNewHp;
    defenderData.splashedNewHp = splashedNewHp;
    defenderData.defenseDamage = Math.min(defenseDamage, attackerData.hp - attackerNewHp);;


    this.battleResult =  {
      attacker: attackerData,
      defender: defenderData
    };
    this.change.emit(this.battleResult);

    this.googleAnalytics.emitEvent(
      'one-on-one calculate', 'method', `${attackerData.unit} vs ${defenderData.unit}`,
      { attacker: attackerData, defender: defenderData }, 1);
  }
}
